<template>

<div id="app">
    <center/>

      <router-view :key="$route.fullPath"></router-view> <!-- 路由出口 -->

  </div>
</template>

<script>
import center from './components/center.vue'
export default {
  name: 'app',
  components: {
	center,//注册
  },
  computed: {
    key() {
      return this.$route.fullPath;
    }
  },
  mounted() {
    // 确保 AMap 已加载（从 window 全局获取）
    const AMap = window.AMap;
    if (!AMap) return;
  
    // 初始化地图（示例：与原 HTML 逻辑一致的基础配置）
    const map = new AMap.Map('container', {
      center: [118.79, 32.06], // 原逻辑中的南京坐标（可根据需求调整）
      zoom: 12,
      viewMode: '3D',
      resizeEnable: true
    });
  
    // 若需使用原 HTML 中的插件（如 Scale、ToolBar），直接调用即可
    // 示例：添加比例尺控件（原 HTML 中 plugin 已加载该插件）
    const scale = new AMap.Scale();
    map.addControl(scale);
  
    // 若需使用天气插件（原 HTML 中已加载 AMap.Weather）
    AMap.plugin('AMap.Weather', () => {
      const weather = new AMap.Weather();
      // 后续天气查询逻辑可参考原 HTML 补充
    });
  }
}


</script>

