import Vue from 'vue'
import VueRouter from 'vue-router'


// 使用 Vue Router 插件
Vue.use(VueRouter)

// 导入页面组件（可以使用懒加载提高性能）
const Home = () => import(/* webpackChunkName: "home" */ '@/views/Home.vue')
const DTFW = () => import(/* webpackChunkName: "dtfw" */ '@/views/DTFW.vue')
const BDSQ = () => import(/* webpackChunkName: "bdsq" */ '@/views/BDSQ.vue')
const MSCT = () => import(/* webpackChunkName: "msct" */ '@/views/MSCT.vue')
const GWDK = () => import(/* webpackChunkName: "gwdk" */ '@/views/GWDK.vue')
// 定义路由
const routes = [
	{
	      path: '/',
          redirect: '/home',
	
	  },
  {
        path: '/home',
        name: 'Home',
        component: Home,
        meta: {
        title: '宁逛|首页', // 自定义元信息

    }
  },
  {
        path: '/dtfw',
        name: 'DTFW',
        component: DTFW,
        meta: {
        title: '宁逛|地图服务'
    }
  },
   {
        path: '/bdsq',
        name: 'BDSQ',
        component: BDSQ,
        meta: {
        title: '宁逛|八大商圈'
	  }
	},
    {
        path: '/msct',
        name: 'MSCT',
        component: MSCT,
        meta: {
          title: '宁逛|美食餐厅'
        }
      },
    {
        path: '/gwdk',
        name: 'GWDK',
        component: GWDK,
        meta: {
          title: '宁逛|购物打卡'
        }
      },  
]

// 创建路由实例
const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL, // 基础路径
  routes,
  scrollBehavior(to, from, savedPosition) {
    // 路由切换时滚动到顶部
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

// 全局前置守卫
router.beforeEach((to, from, next) => {
  // 设置页面标题
  document.title = to.meta.title || '宁逛|默认标题'

    next() // 确保一定要调用 next()
  })

// 全局后置钩子
router.afterEach((to, from) => {
  // 这里可以添加页面统计等操作
})

export default router